
.calendar-box[data-v-eb230f10]{
    height: 43vh;
    background-color: #fff;
}
.day-box[data-v-eb230f10]{
    background-color: #fff;
    width: calc(100% - 0.5rem);
    margin: .25rem auto 0 auto;
    overflow: hidden;
    border-radius: 5px;
    box-shadow: 0 0 2px #ddd;
}
.day-box .title[data-v-eb230f10]{
    line-height: .9rem;
    text-align: center;
    color: #fff;
    font-size: 16px;
    background:-webkit-gradient(linear, 0% 0%, 0% 100%,from(#ff8922), to(#ffc695));
}
.days[data-v-eb230f10]{
    /*border: solid 1px #ddd;*/
    border-top: none;
    box-sizing: border-box;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}
.days .day-item[data-v-eb230f10]{
    line-height: .9rem;
}
.days .day-item[data-v-eb230f10]:before{
    content: "";
    display: inline-block;
    width: .1rem;
    height: .1rem;
    border-radius: 50%;
    background-color: #ddd;
    margin-right: 0.2rem;
    margin-left: 0.2rem;
}
.days .day-item span[data-v-eb230f10]{
    display: inline-block;
    width: calc(100% - 0.1rem - 2*0.2rem);
    float: right;
}
.days .day-item.warn[data-v-eb230f10]{
    color: #ff6054;
}
.days .day-item.warn[data-v-eb230f10]:before{
    background-color: #ff6054;
}
.tool[data-v-eb230f10]{
    background-color: #fff;
    margin-bottom: 0.25rem;
    padding: 0.15rem 0;
}
.tool .time-box[data-v-eb230f10]{
    width: 86%;
    margin: 0 auto;
    font-size: 0;
    height: 0.8rem;
    overflow: hidden;
}
.time-box span[data-v-eb230f10]{
    display: inline-block;
    font-size: 16px;
}
.time-box span[data-v-eb230f10]:nth-child(1),.time-box span[data-v-eb230f10]:nth-child(3){
    width: 24%;
}
.time-box span[data-v-eb230f10]:nth-child(1){
    color: #999999;
}
.time-box span[data-v-eb230f10]:nth-child(2){
    width: 52%;
}
.time-box span[data-v-eb230f10]:nth-child(3){
    text-align: right;
    color: #3497f5;
}
.rect-icon[data-v-eb230f10]{
    position: relative;
    top: 0.16rem;
    display: inline-block;
    border: solid 1px #3497f5;
    height: 0.4rem;
    width: 0.2rem;
    padding: 2px 4px;
    border-radius: 3px;
}
.rect-icon .iconfont[data-v-eb230f10]{
    position: absolute;
    top: 0.118rem;
    left: 0.05rem;
    font-size: 0.8em;
}
.time-box .iconfont[data-v-eb230f10]{
    display: inline-block;
    color: #3497f5;
    font-weight: 800;
}
.right-arrow[data-v-eb230f10]{
    position: relative;
    transform:rotate(180deg);
    z-index: 11;
}
.time-box .time-text[data-v-eb230f10]{
    position: relative;
    top: 0.24rem;
    display: inline-block;
    width: calc(100% - 0.4rem - 20px);
    text-align: center;
    color: #333;
}
.time-box .time-text input[data-v-eb230f10]{
    position: absolute;
    left: 0;top: -2px;
    width: 60%;
    opacity: 0;
    z-index: 10;
}
.time-box .time-text span[data-v-eb230f10]{
    display: block;
}
@media screen and (max-width: 320px) {
.time-box span[data-v-eb230f10]{
        font-size: 14px;
}
.time-box .time-text[data-v-eb230f10]{
        top: 0.26rem;
}
}



