.body-box {
    background-color: #f2f2f2;
}
/*tab */
.tab-box {
    height: 60px;
    line-height: 60px;
    text-align: center;
    padding: 0 0.5rem;
    background-color: #fff;
    display: flex;
    vertical-align: middle;
}
.tab-box .tap-wrap {
    flex: 1;
    color: #333;
}
.tab-box .tap-btn {
    height: 100%;
    font-size: 15px;
    box-sizing: border-box;
    border-bottom: 2px solid #fff;
}
.selected {
    color: #3497f5 !important;
    position: relative;
}
.selected::after {
    content: '';
    width: 100%;
    height: 2px;
    background-color: #3497f5;
    display: inline-block;
    position: absolute;
    bottom: 0;
    right: 0;
    left:0;
}
.tab-box i {
    height: 100%;
    margin: 0 0.4rem;
}
.tab-box i::after {
    content: '';
    height: 100%;
    display: inline-block;
    vertical-align: middle;
}
.tab-box i::before {
    content: '';
    width: 1px;
    height: 16px;
    background: #aaa;
    display: inline-block;
    vertical-align: middle;
}
/* */
.approval {
    background-color: #fff;
}
.batch-approval {
    height: 1rem;
    line-height: 1rem;
    text-align: right;
    margin-top: 0.25rem;
    padding: 0 0.3rem;
}
.batch-approval .batch-approval-text {
    color: #666;
    font-size: 14px;
    margin-right: 0.2rem;
    display: inline-block;
    vertical-align: middle;
}
.batch-approval .mint-switch {
    height: 32px;
    font-size: 0;
    display: inline-block;
    vertical-align: middle;
}
/* */
.approval-list {
    margin: 0 0.3rem;
}
.approval-list .approval-list-cell {
    overflow: hidden;
}
.approval-list-cell .approval-name {
    float: left;
    color: #fff;
    width: 1.4rem;
    height: 1.4rem;
    font-size: 18px;
    line-height: 1.4rem;
    text-align: center;
    border-radius: 100%;
    margin-top: 0.5rem;
    background-color: #6eb4f6;
}
.approval-list .approval-list-cell .approval-info {
    padding: 0.3rem 0;
    margin-left: 1.7rem;
}
.approval-list .approval-list-cell a:last-child .approval-info{
    border-bottom: 0;
}
.approval-info p {
    margin-bottom: 0.2rem;
}
.approval-info .approval-title-date {
    margin-bottom: 0.3rem;
}
.approval-info .approval-title {
    color: #333;
    font-size: 18px;
}
.approval-info .approval-date {
    color: #666;
    float: right;
    font-size: 16px;
    line-height: 27px;
}
.approval-info .approval-start-date,.approval-info .approval-end-date{
    color: #999;
    font-size: 16px;
}
.approval-info .approval-state {
    color: #ff8922;
    font-size: 16px;
    margin-bottom:0px;
}

/*  */
.approved-head {
    padding: 0.3rem 0.25rem;
    background-color: #fff;
}
.approved-head .approved-head-row {
    color: #333;
    width: 100%;
    margin-bottom: 0.25rem;
    overflow: hidden;
}
.approved-head .approved-head-row:last-child {
    margin-bottom: 0;
}
.approved-head .approved-head-warp {
    width: 50%;
    float: left;
}
.approved-head .approved-head-info {
    color: #999;
}
/* select,input */
input,select {
    color: #999;
    border: 0;
    height: 0.6rem;
    line-height: 0.6rem;
    outline:none;
    background-color: #fff;
    box-sizing: border-box;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}
.selectWrap {
    position: relative;
    line-height: normal;
    display: inline-block;
    border-radius: 5px;
    border: 1px solid #3497f5;
}

.selectWrap select {
    width: 100%;
    padding: 0 0.5rem 0 0.1rem;
    border-radius: 5px;
}
.selectBtn {
    width: 0.4rem;
    border-radius: 0 5px 5px 0;
    position: absolute;
    top: -1px;
    bottom: -1px;
    right: -1px;
    overflow: hidden;
    background-color: #3497f5;
}
.selectBtn::before,.selectBtn::after {
    content: "";
    width: 100%;
    height: 50%;
    display: inline-block;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 50%;
    background-image: url('../../assets/img/icon-select.png');
    position: absolute;
}
.selectBtn::before {
    top: 0;
    right: 0;
    transform: rotate(180deg);
}
.selectBtn::after {
    bottom: 0;
    right: 0;
}
.addBtn {
    width: 0.35rem;
    height: 0.35rem;
    line-height: 0.35rem;
    display: inline-block;
    vertical-align: middle;
    border-radius: 100%;
    background-color: #9ccfff;
    background-size: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../assets/img/icon-add.png');
}
/* select end*/

/*  */
.mint-cell-wrapper {
    padding: 0;
}
.application-list-box{
    padding: 0 0.3rem;
    background-color: #fff;
}
.application-type-box {
    display: flex;
    padding: 0.4rem 0;
}
.application-type-box .application-type {
    flex:1;
    color: #fff;
    font-size: 14px;
    padding: 0.2rem 0;
    border-radius: 5px;
    text-align: center;
    margin-left: 0.3rem;
    background-color: #aaa;
}
.application-type-box .nnsubmitted {
    background-color: #ff8922;
}
.application-type-box .approved {
    background-color: #11bf84;
}
.application-type-box .retreated {
    background-color: #f3725f;
}
.application-type-box .approvaling {
    background-color: #5ac0de;
}
.application-type-box .application-type:first-child {
    margin-left: 0;
}

.application-list-box .application-list{
}
.application-list .application-list-cell {
    display: flex;
    align-items: center;
}
.application-list .icon {
    width: 0.7rem;
    height: 0.7rem;
    border-radius: 5px;
    background-color: #bbb;
    background-image: url('../../assets/img/workLunch-icon.png');
    background-repeat: no-repeat;
    background-size: 65%;
    background-position: center;
}
.application-list .application-list-info {
    flex: 1;
    font-size: 14px;
    padding: 0.35rem 0;
    margin-left: 0.3rem;
}
.application-list .application-title-date {
    margin-bottom: 0.3rem;
}
.application-list .application-title {
    color: #000;
    font-size: 16px;
}
.application-list .application-date {
    color: #666;
    float: right;
    line-height: 21px;
}
.application-list .eat-type {
    color: #999;
}
.application-list .application-title span {
    color: #aaa;
}
.application-list .application-title .nnsubmitted {
    color: #ff8922;
}
.application-list .application-title .approved {
    color: #11bf84;
}
.application-list .application-title .retreated {
    color: #f3725f;
}
.application-list .application-title .approvaling {
    color: #5ac0de;
}

.application-list a:last-child .application-list-cell .bd-bottom-1::before {
    border-bottom:0;
}

@media screen and (max-width: 320px) {
    .application-type-box .application-type {
        font-size: 12px
    }
    .application-list .application-list-info {
        font-size: 12px;
    }
    .application-list .application-title {
        color: #000;
        font-size: 14px;
    }
    .application-list .application-date {
        line-height: 19px;
    }
}
/* end */



